@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
  text-align: center;
  font-family: 'Poppins', sans-serif;
}

body {
  background: linear-gradient(135deg, rgba(173, 216, 230, 0.25), rgba(255, 182, 193, 0.25), rgba(221, 160, 221, 0.25));
}

/* LOADING SPINNER */
.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid transparent;
  border-radius: 50%;
  border-top: 4px solid transparent;
  border-image: linear-gradient(135deg, #59d5ff, #ff89bb, #ff70ff) 1;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-spinner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.loading-text {
  margin-top: 1rem;
  font-size: 1rem;
  color: #555;
  font-family: 'Poppins', sans-serif;
  font-size: large;
}

/* USER FORM */
.form-container {
  max-width: 400px;
  margin: 30px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #ffffff;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.form-container input {
  width: 100%;
  padding: 10px;
  margin-bottom: 12px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  box-sizing: border-box;
}

.form-container button {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.6s ease, color 0.3s ease;
  color: #3e3e3e;
}

.form-container button:enabled {
  background: linear-gradient(135deg, rgba(89, 213, 255, 0.25), rgba(255, 137, 155, 0.25), rgba(255, 112, 255, 0.25));
}

.form-container button:hover:enabled {
  filter: brightness(1.1);
}

.form-container button:disabled {
  background-color: #eaeaea;
  color: #ffffff;
  cursor: not-allowed;
}

/* FETCHED USERS */
.fetch-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  font-family: Arial, sans-serif;
}

.fetch-box {
  border: 2px solid #ffffff;
  padding: 2rem;
  border-radius: 8px;
  width: 100%;
  max-width: 500px;
  background: linear-gradient(135deg, rgba(89, 213, 255, 0.25), rgba(255, 137, 155, 0.25), rgba(255, 112, 255, 0.25));
  backdrop-filter: blur(4px);
}

.fetch-subtitle {
  text-align: center;
  font-weight: bold;
  color: #373737;
  margin-bottom: 1.5rem;
}

.fetch-users {
  list-style: none;
  padding: 0;
  margin: 0;
}

.fetched-item {
  background-color: #ffffff;
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 0.8rem 1rem;
  margin-bottom: 0.8rem;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.fetched-item:hover {
  background-color: #373737;
}

.fetched-item:hover .fetched-name {
  color: #ffffff;
}

.fetched-name {
  font-size: 1rem;
  font-weight: 500;
  color: #373737;
}

/* USER MANAGEMENT */
.user-list-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  font-family: Arial, sans-serif;
}

.user-title {
  font-size: 2rem;
  font-weight: bold;
  color: #373737;
  margin-bottom: 1rem;
}

.user-box {
  border: 2px solid #ffffff;
  padding: 2rem;
  border-radius: 8px;
  width: 100%;
  max-width: 500px;
  background: linear-gradient(135deg, rgba(89, 213, 255, 0.25), rgba(255, 137, 155, 0.25), rgba(255, 112, 255, 0.25));
  backdrop-filter: blur(4px);
}

.user-list {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  max-width: 500px;
}

.user-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 0.8rem 1rem;
  margin-bottom: 0.8rem;
}

.user-name {
  font-size: 1rem;
  font-weight: 500;
  color: #000000;
}

.delete-button {
  padding: 6px 12px;
  background-color: rgb(255, 25, 25);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.delete-button:hover {
  background-color: rgb(150, 14, 14);
}